<template>
  <div class="gun">
    <el-button type="primary" @click="shuffle" v-if="game">开始游戏</el-button>
    <el-button type="primary" @click="openGun" v-else>开枪</el-button>
  </div>
</template>

<script>
export default {
  name: 'gun',
  data() {
    return {
      game: true,
      bullet: [0, 1, 2, 3, 4, 5], //子弹
      luck: '', // 当前子弹
      currentIndex: 0 // 第几枪
    }
  },
  methods: {
    openGun() {
        console.log(this.luck)
      if (this.bullet[this.currentIndex] === this.luck) {
        this.$alert('OK，你已经死亡！', '', {
          confirmButtonText: '确定',
          callback: () => {
            this.game = true
          }
        })
      }
      this.currentIndex++
    },
    shuffle() {
      let len = this.bullet.length
      this.game = false
      this.currentIndex = 0
      this.luck = this.bullet[parseInt(Math.random() * (len - 1))]
      for (let i = 0; i < len; i++) {
        let index = parseInt(Math.random() * (len - 1))
        let temp = this.bullet[index]
        this.bullet[index] = this.bullet[len - i - 1]
        this.bullet[len - i - 1] = temp
      }
      console.log(this.bullet)
    }
  }
}
</script>

